@import '../../../assets/scss/base';

.uploaded_file {
    border: 1px solid $grey-color-700;
    @include border-radius (5px);
    background: $white-color;  
    display: $inline-block;
    // position: $absolute;
    // bottom: 0;
    // left: 0;
    img {
        width: 70px;
        @include border-radius (5px); 
        object-fit: $cover;
        height: $auto; 
        max-height: 70px;
        object-position: top;
    }
}