@import '../../../../assets/scss/base';

.range_slider {
    min-height: 44px;
    .rc-slider {
        .rc-slider-track {
            background-color: $secondary-color;
        }
        .rc-slider-handle {
            @include opacity (1);
            background: $dark-green-color;
            width: 16px; height: 16px;
            border-color: $white-color;
        }
    }
}
.admin_panel {
    .range_slider {
        min-height: 44px;
        .rc-slider {
            .rc-slider-track {
                background-color: $dark-green-color;
            }
            .rc-slider-rail  {
                background-color: $white-color;
            }
            .rc-slider-handle {
                @include opacity (1);
                background: $secondary-color;
                width: 16px; height: 16px;
                border-color: $white-color;
                &.active, &.rc-slider-handle-dragging {
                    box-shadow: 0 0 0 5px $base-color;
                }
            }
        }
        span {
            color: $base-color !important;
        }
    }
}