@import "assets/scss/base";
@import "assets/scss/theme.scss";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

// Btn Group
.btn_group {
  .btn {
    margin: 5px;
  }
  &.disabled {
    @include opacity(0.4);
    color: $white-color;
    pointer-events: $none;
  }
}

// Tooltips
div.MuiTooltip {
  &-tooltip {
    padding: 5px;
    color: $base-color;
    background-color: $white-color;
  }
}

.reactEasyCrop_Container {
  background: $white-color;
  @include border-radius(8px);
  z-index: 11;
  .btn {
    position: $relative;
    z-index: 111;
  }
}

.no_result {
  text-align: $center;
  display: $block;
}

.btn-badge {
  position: $relative;
  img + img {
    position: $absolute;
    left: 50%;
    // top: 50%;
    @include transform(translate(-50%));
  }
}

.ver_icon {
  img {
    min-width: 18px;
  }
}

// React Country Dropdown
div.country_dropdown {
  .react-tel-input {
    padding: 0;
    border: 0;
    &:focus {
      background: $transparent;
    }
    .form-control {
      @extend .form-control;
      background: $grey-color-500;
      @include font-size(1);
      padding-left: 78px;
      width: 100%;
    }
    .flag-dropdown {
      background: $transparent;
      border: 0;
      left: 10px;
      &.open {
        background-color: $transparent;
      }
      .selected-flag {
        background-color: $transparent !important;
        &:focus {
          background-color: $transparent !important;
        }
        .arrow {
          background: url(assets/icons/down_arrow.svg) $no-repeat;
          background-size: 100%;
          width: 16px;
          height: 10px;
          border: 0;
          left: 30px;
          margin-top: -5px;
        }
      }
    }
  }
}

// Country Dropdown
.country_picker {
  // @extend .form-control;
  @include flexbox;
  padding: 0;
  select {
    width: 100%;
    line-height: 1;
    @extend .form-control;
    &[name="rcrs-country"] {
      background-color: $grey-color-500;
      @include border-radius (10px);
    }
    &[name="rcrs-region"] {
      width: 100%;
      background-color: $grey-color-500;
      @include border-radius (10px);
    }
  }
}

.admin_panel {
  .country_picker {
    select {
      &[name="rcrs-country"] {
        background-color: $white-color;
      }
      &[name="rcrs-region"] {
        background-color: $white-color;
      }
    }
  }
  .filters_col {
    .form-control {
      background-color: $white-color;
    }
  }
}

.tool_tip {
  position: $relative;
  display: $inline-block;
  .tooltiptext {
    visibility: $hidden;
    white-space: normal;
    word-break: break-word;
    background-color: $base-color;
    @include border-radius(6px);
    min-width: 140px;
    max-width: 400px;
    color: $white-color;
    text-align: $center;
    padding: 5px 5px 5px;
    max-height: 120px;
    overflow-y: $auto;
    font-size: 12px;
    position: $absolute;
    left: -50%;
    transform: translateX(calc(50% - 115px));
    bottom: 100%;
    z-index: 11;
  }
  &:hover .tooltiptext {
    visibility: $visible;
  }
}
